<template>
  <div
      class="content_wrapper"
      ref="content_wrapper"
  >
    <div
        class="content"
        :style="{ width: width + 'px', height: height + 'px', display: display }"
    >
      <slot v-if="display === 'block'"></slot>
    </div>
  </div>
</template>

<script>
export default {
  name: 'RatioKeeper',
  props: {
    ratio: Number,
  },
  data() {
    return {
      width: 0,
      height: 0,
      display: 'none',
    }
  },
  watch: {
    ratio() {
      this.keepAspectRatio()
    },
  },
  methods: {
    keepAspectRatio() {
      let wrapperWidth = this.$refs.content_wrapper.clientWidth
      let wrapperHeight = this.$refs.content_wrapper.clientHeight

      if (wrapperWidth / wrapperHeight > this.ratio) {
        // нужно добавлять поля по бокам
        this.width = wrapperHeight * this.ratio
        this.height = wrapperHeight
      } else {
        // нужно добавлять поля сверху
        this.width = wrapperWidth
        this.height = wrapperWidth / this.ratio
      }
    },
  },
  created() {
    window.addEventListener('resize', this.keepAspectRatio)
  },
  mounted() {
    this.keepAspectRatio()
    this.display = 'block'
  },
  destroyed() {
    window.removeEventListener('resize', this.keepAspectRatio)
  },
}
</script>

<style scoped lang="scss">
.content_wrapper {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;

  .content {
    width: 100px;
    height: 100px;
    background-color: white;
  }
}
</style>
