<template>
  <div
      ref="movable"
      class="movable"
      :class="{focused: isFocused, selected: isSelected, outside: isOutside, inactive: !isFocused && !isSelected}"
      @dblclick="onDoubleClick"
      :tabindex="element.id"
      @focusin="isFocused = true"
      @focusout="isFocused = false; isSelected = false"
      @keyup="onKeyUp"
  >
    <div>
      <b>{{ element.id }}</b>
      <br>{{ position }}
      <br>{{ size }}
      <br>isFocused: {{ isFocused }}
      <br>isSelected: {{ isSelected }}
      <br>isMoving: {{ isMoving }}
      <br>isOutside: {{ isOutside }}
    </div>

    <template v-if="isFocused">
      <div class="size_change top" ref="top"></div>
      <div class="size_change right" ref="right"></div>
      <div class="size_change bottom" ref="bottom"></div>
      <div class="size_change left" ref="left"></div>
    </template>

    <!--    <component :is="content.component" v-bind="content"/>-->
  </div>
</template>

<script>
import TextObject from "@/components/slide-elements/TextObject";
import YouTubeObject from "@/components/slide-elements/YouTubeObject";
import {DELETE_SLIDE_ELEMENT} from "@/store/mutation-types";

export default {
  name: 'SlideElement',
  components: {
    // eslint-disable-next-line vue/no-unused-components
    TextObject,
    // eslint-disable-next-line vue/no-unused-components
    YouTubeObject,
  },
  props: ['element', 'slideWidth', 'slideHeight', 'cellSize'],
  data() {
    return {
      isFocused: false,
      isSelected: false,
      isMoving: false,

      defaultZ: '20',
      activeZ: '21',

      x: 0,
      y: 0,
      width: 0,
      height: 0,
    }
  },
  computed: {
    position() {
      return [this.x, this.y]
    },
    size() {
      return [this.width, this.height]
    },
    isOutside() {
      return this.x < 0
          || this.y < 0
          || this.x + this.width > this.slideWidth
          || this.y + this.height > this.slideHeight
    }
  },
  watch: {
    position() {
      this.updatePosition()
    },
    size() {
      this.updateSize()
    },
    isFocused(newVal) {
      if (newVal) {
        setTimeout(() => this.initializeSizeChanger(this.$refs.top, false, true), 100)
        setTimeout(() => this.initializeSizeChanger(this.$refs.right, true, false), 100)
        setTimeout(() => this.initializeSizeChanger(this.$refs.bottom, false, false), 100)
        setTimeout(() => this.initializeSizeChanger(this.$refs.left, true, true), 100)
      }
    }
  },
  methods: {
    getPosition() {
      return {x: this.x, y: this.y}
    },
    getSize() {
      return {width: this.width, height: this.height}
    },

    initializeMovable() {
      let el = this.$refs.movable
      el.ondragstart = function () {
        return false;
      };

      el.onmousedown = (event) => {
        this.isMoving = true
        el.style.zIndex = this.activeZ
        let initialX = event.clientX, initialY = event.clientY

        let posBeforeMove = this.getPosition()

        let onMouseMove = (event) => {
          let movedCellsX = Math.round((event.clientX - initialX) / this.cellSize)
          let movedCellsY = Math.round((event.clientY - initialY) / this.cellSize)

          this.x = posBeforeMove.x + movedCellsX
          this.y = posBeforeMove.y + movedCellsY
        }

        document.addEventListener('mousemove', onMouseMove);

        document.onmouseup = () => {
          if (this.isOutside) {
            if (this.x < 0) this.x = 0
            if (this.y < 0) this.y = 0
            if (this.x + this.width > this.slideWidth) this.x = this.slideWidth - this.width
            if (this.y + this.height > this.slideHeight) this.y = this.slideHeight - this.height
          }

          this.isMoving = false
          el.style.zIndex = this.defaultZ
          document.removeEventListener('mousemove', onMouseMove);
          document.onmouseup = null;
        }
      };
    },

    // eslint-disable-next-line no-unused-vars
    initializeSizeChanger(el, isHorizontal, shouldChangePos) {
      el.ondragstart = function () {
        return false;
      };

      el.onmousedown = (event) => {
        event.stopPropagation()

        this.isMoving = true
        el.style.zIndex = this.activeZ
        let initialX = event.clientX, initialY = event.clientY

        let sizeBeforeMove = this.getSize()
        let posBeforeMove = this.getPosition()

        let onMouseMove = (event) => {
          let movedCellsX = Math.round((event.clientX - initialX) / this.cellSize)
          let movedCellsY = Math.round((event.clientY - initialY) / this.cellSize)

          if (isHorizontal) {
            if (shouldChangePos) {
              this.x = posBeforeMove.x + movedCellsX
              this.width = sizeBeforeMove.width - movedCellsX
            } else {
              this.width = sizeBeforeMove.width + movedCellsX
            }
          } else {
            if (shouldChangePos) {
              this.y = posBeforeMove.y + movedCellsY
              this.height = sizeBeforeMove.height - movedCellsY
            } else {
              this.height = sizeBeforeMove.height + movedCellsY
            }
          }
        }

        document.addEventListener('mousemove', onMouseMove);

        document.onmouseup = () => {
          // if (this.isOutside) {
          //   if (this.x < 0) this.x = 0
          //   if (this.y < 0) this.y = 0
          //   if (this.x + this.width > this.slideWidth) this.x = this.slideWidth - this.width
          //   if (this.y + this.height > this.slideHeight) this.y = this.slideHeight - this.height
          // }

          this.isMoving = false
          el.style.zIndex = this.defaultZ
          document.removeEventListener('mousemove', onMouseMove);
          document.onmouseup = null;
        }
      };
    },

    updatePosition() {
      this.$emit('update-pos', {
        el: this.$refs.movable,
        position: this.position,
      })
    },
    updateSize() {
      this.$emit('update-size', {
        el: this.$refs.movable,
        size: this.size,
      })
    },

    onKeyUp(event) {
      console.log()
      if (event.key === 'Delete')
        this.$store.commit(DELETE_SLIDE_ELEMENT, this.element.id)
    },

    onDoubleClick() {
      this.isSelected = !this.isSelected;
    },
  },
  created() {
    Object.entries(this.element).forEach(([k, v]) => this[k] = v)
  },
  mounted() {
    this.initializeMovable()
    this.updatePosition()
    this.updateSize()
  },
}
</script>

