.movable {
  position: absolute;
  z-index: $defaultZ;
  //background-color: rgba(226, 226, 226, 0.3);
  border: 2px rgba(217, 217, 217, 1) dashed;

  box-sizing: border-box;

  background-color: white;
  //border: 1px $primary dashed;

  cursor: move;
