  outline: none;

  display: flex;
  justify-content: center;
  align-items: center;

  &:hover {
    border: 2px $primary solid;
    //padding: -2px;
    border-radius: 2px;
  }

  * {
    //pointer-events: none;
  }

  &.outside {
    opacity: .3;
  }

  &.focused {
    //pointer-events: auto;

    border: 2px $primary solid;
    //padding: -2px;
    border-radius: 2px;
    //cursor: default;
  }

  &.selected {
  }

  .size_change {
    $squareSize: 6px;
    width: $squareSize;
