    background-color: $primary;
    position: absolute;
    z-index: $defaultZ + 1;

    &.top {
      top: 0;
      transform: translateY(calc(-50% - 1px));
      cursor: ns-resize;
    }

    &.right {
      right: 0;
      transform: translateX(calc(50% + 1px));
      cursor: ew-resize;
    }

    &.bottom {
      bottom: 0;
      transform: translateY(calc(50% + 1px));
      cursor: ns-resize;
    }

    &.left {
      left: 0;
      transform: translateX(calc(-50% - 1px));
      cursor: ew-resize;
    }
  }
}
</style>
