<template>
  <div
      class="slide"
      ref="slide"
  >
    <div
        v-if="isGridShowing"
        class="grid"
        :style="{'background-size': backgroundSize}"
    ></div>

    <SlideElement
        v-for="slideElement in slideElements"
        :key="slideElement.id"
        v-bind="{element: slideElement, slideWidth, slideHeight, cellSize}"
        @update-pos="updateElementPosition"
        @update-size="updateElementSize"
    />
  </div>
</template>

<script>
import SlideElement from "@/components/SlideElement";
import {mapState} from "vuex";
import {SET_SLIDE_SIZE} from "@/store/mutation-types";

export default {
  name: 'Slide',
  components: {SlideElement},
  props: {},
  data() {
    return {
      backgroundSize: 'calc(100% / 16) calc(100% / 9)',
      multiplier: 4,

      isGridShowing: true,
    }
  },
  computed: {
    ...mapState({
      slideElements: 'slideElements',
      slideWidth: state => state.slide.width,
      slideHeight: state => state.slide.height,
      cellSize: state => state.slide.cellSize,
    }),
  },
  watch: {},
  methods: {
    test() {
      console.log('test')
    },

    setGridSize() {
      this.$store.commit(SET_SLIDE_SIZE, {
        width: this.slideWidth * this.multiplier,
        height: this.slideHeight * this.multiplier,
        cellSize: this.$refs.slide.clientWidth / (this.slideWidth * this.multiplier),
      })
      this.backgroundSize = `${100 / this.slideWidth}% ${100 / this.slideHeight}%`
    },

    updateElementPosition({el, position}) {
      el.style.left = position[0] * this.cellSize + 'px'
      el.style.top = position[1] * this.cellSize + 'px'
    },

    updateElementSize({el, size}) {
      el.style.width = size[0] * this.cellSize + 'px'
      el.style.height = size[1] * this.cellSize + 'px'
    },
  },
  created() {

  },
  mounted() {
    this.setGridSize()
  },
}
</script>

<style scoped lang="scss">
$gridColor: rgb(234, 234, 234);
$gridWidth: 1px;

.slide {
  position: relative;
  width: 100%;
  height: 100%;

  .grid {
    position: absolute;
    width: 100%;
    height: 100%;
    background-image: linear-gradient(to right, $gridColor $gridWidth, transparent 1px),
    linear-gradient(to bottom, $gridColor $gridWidth, transparent $gridWidth);
    border-bottom: $gridColor $gridWidth solid;
    border-right: $gridColor $gridWidth solid;
  }
}
</style>
